#product{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: white;
    box-shadow: 2px 2px 6px 0px #8080803b;
}

#product img{
    width: 300px;
    object-fit: contain;
    margin: 20px;
}   